import React, { Component } from 'react'
import {news, detailNews, events} from './data';
const ProductContext = React.createContext();

//Provider
//Consumer

class ProductProvider extends Component {
    state = {
        news: [],
        events:[],
        detailProduct: detailNews,
    };
    componentDidMount() {
        this.setNews();
        this.setEvents()
    }

    setNews = () => {
        let tempProducts = [];
        news.forEach(item => {
            const singleItem = { ...item };
            tempProducts = [...tempProducts, singleItem];
        });

        this.setState(() => {
            return { news:tempProducts };
        },this.checkCartItems);
    };

    setEvents = () => {
        let tempProducts = [];
        events.forEach(item => {
            const singleItem = { ...item };
            tempProducts = [...tempProducts, singleItem];
        });

        this.setState(() => {
            return { events:tempProducts };
        },this.checkCartItems);
    };




    getNews = id => {
        const product = this.state.news.find(item => item.id === id);
        return product;
    };



    NewsDetail = id => {
          const product = this.getNews(id);
          this.setState(() => {
              return { detailProduct: product };
          });
    };


    getEvent = id => {
        const product = this.state.events.find(item => item.id === id);
        return product;
    };

    EventsDetail = id => {
        const product = this.getEvent(id);
        this.setState(() => {
            return { detailProduct: product };
        });
    };




       





        render() {
            return (
                <ProductContext.Provider 
                value={{
                ...this.state,  
                handleNews: this.NewsDetail,
                handleEvent: this.EventsDetail,
                }}
                >           
                {this.props.children}
                </ProductContext.Provider>
            )
        }
    }


const ProductConsumer  = ProductContext.Consumer;

export { ProductProvider, ProductConsumer}