import React, { Component } from 'react';
import News from './News';
import {ProductConsumer} from '../context';
import {Link} from "react-router-dom";

export default class NewsList extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="interesting__container interesting">
                    <div className="container">
                        <div className="grid grid--interesting" >
                            <div className="heading-container" >
                                <div className="heading heading--grid" data-v-8578b8a4 ><Link to="/news"  data-v-8578b8a4>Новости</Link>
                                    <Link to={"/news"}  data-v-8578b8a4>Все новости</Link></div>
                            </div>
                            <ProductConsumer>
                                {value=> {
                                    return value.news.map( news => {return <News key={news.id} product={news} />;})
                                }}
                            </ProductConsumer>
                        </div>
                        <div className="more more--next" >
                            <button className="button">Загрузить ещё</button>
                        </div>
                    </div>
                </div>


            </React.Fragment>      
        );
    }
}


