import React, { Component } from "react";
import Slider from "react-slick";
import img1 from '../../../../img/slides/slide1.png'
import img2 from '../../../../img/slides/slide2.png'
import img3 from '../../../../img/slides/slide3.png'
import img4 from '../../../../img/slides/slide4.JPG'
import mobIMG1 from '../../../../img/slides/MobileSlides/MobileSlide1.jpg'
import './SimpleSlider.css'
import Slide from "./Slide/Slide";
import NavSlide from "./NavSlide/NavSlide";



export default class SimpleSlider extends Component {
    constructor(props) {
        super(props);
        this.state = {
            nav1: null,
            nav2: null
        };
    }

    componentDidMount() {
        this.setState({
            nav1: this.slider1,
            nav2: this.slider2
        });
    }
    render() {

        const settings = {
            arrows:true,
            dots:false,
            slidesToShow:1,
            autoplay:true,
            speed:500,
            autoplaySpeed:6000,
            asNavFor: this.state.nav2,
            ref: slider => (this.slider1 = slider),

            responsive:[
                {
                    breakpoint: 768,
                    settings: {
                        slidesToShow:1
                    }
                },
                {
                    breakpoint: 768,
                    settings: {
                        dots:true
                    }
                }
            ]
        };

        const settings2 = {
            arrows:false,
            dots:false,
            slidesToShow:2,
            asNavFor: this.state.nav1,
            ref: slider => (this.slider2 = slider),
            swipeToSlide: true,
            focusOnSelect: true,


            responsive:[
                {
                    breakpoint: 769,
                    settings: {
                        slidesToShow:2
                    }
                },
                {
                    breakpoint: 551,
                    settings: {
                        slidesToShow:1
                    }
                },
                {
                    breakpoint: 1025,
                    settings: {
                        slidesToShow:2
                    }
                },
                {
                    breakpoint: 1441,
                    settings: {
                        slidesToShow:3
                    }
                }
            ]
        };
        return (
            <div className="wrapper">
                <div className="slider">
                    <Slider  {...settings}>

                        <div className="slider__item">
                            <Slide img={img2}
                                   imgMobile={img2}
                                   title={"ОТМЕЧАЕМ НАУРЫЗ ВМЕСТЕ”"}
                                   content={"Будем отмечать Наурыз! Вас ждут национальные игры, музыка и угощения "}
                                   type={'Мероприятия'}
                                   data={"22.03.2021"}>
                            </Slide>
                        </div>

                        <div className="slider__item filter">
                            <Slide img={img3}
                                   imgMobile={img3}
                                   title={"ОНЛАЙН ПРОФОРИЕНТАЦИЯ"}
                                   content={"В быстроменяющемся современном мире выбор профессии одна из важнейших и сложных задач. Подготовка молодежи к выбору профессии - профориентация, полезный и важный вид занятий, помогающий определиться с выбором профессии."}
                                   type={'ZOOM-КОНФЕРЕНЦИЯ'}
                                   data={"10.04.2021"}>/</Slide>
                        </div>
                        <div className="slider__item filter">
                            <Slide img={img4}
                                   imgMobile={img4}
                                   title={"Прощай коворкинг, привет новый офис @qala.lab"}
                                   content={"Здесь проводили фотосушку сообщество плёночных фотографов «Свет», играли Настольщики @boardgamesaktobe, работал @turlanovaidar , наш первый резидент коворкинга, проводили фотосессии так и профессиональные фотографы, так и наши посетители арт-пространства. "}
                                   type={'НОВОСТИ'}
                                   data={"27.04.2021"}/>
                        </div>


                    </Slider>

                    <div className="nav-slider" >
                        <div className="container">
                            <svg aria-hidden="true"
                                 focusable="false"
                                 data-prefix="fa"
                                 data-icon="chevron-left"
                                 role="img"
                                 xmlns="http://www.w3.org/2000/svg"
                                 viewBox="0 0 320 512"
                                 className="nav-slider__control nav-slider__control--prev svg-inline--fa fa-chevron-left fa-w-10">
                                <path data-v-74622ff2=""
                                      fill="currentColor"
                                      d="M34.52 239.03L228.87 44.69c9.37-9.37 24.57-9.37 33.94 0l22.67 22.67c9.36 9.36 9.37 24.52.04 33.9L131.49 256l154.02 154.75c9.34 9.38 9.32 24.54-.04 33.9l-22.67 22.67c-9.37 9.37-24.57 9.37-33.94 0L34.52 272.97c-9.37-9.37-9.37-24.57 0-33.94z"
                                      className=""
                                      data-darkreader-inline-fill=""/>
                            </svg>
                            <svg  aria-hidden="true"
                                  focusable="false"
                                  data-prefix="fa"
                                  data-icon="chevron-right"
                                  role="img"
                                  xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"
                                  className="nav-slider__control nav-slider__control--next svg-inline--fa fa-chevron-right fa-w-10">
                                <path data-v-74622ff2="" fill="currentColor"
                                      d="M285.476 272.971L91.132 467.314c-9.373 9.373-24.569 9.373-33.941 0l-22.667-22.667c-9.357-9.357-9.375-24.522-.04-33.901L188.505 256 34.484 101.255c-9.335-9.379-9.317-24.544.04-33.901l22.667-22.667c9.373-9.373 24.569-9.373 33.941 0L285.475 239.03c9.373 9.372 9.373 24.568.001 33.941z"
                                      className="" />
                            </svg>
                            <Slider className={"nav-slider__wrapper"} {...settings2}>

                                <NavSlide img={img2}
                                          title={"ОТМЕЧАЕМ НАУРЫЗ ВМЕСТЕ”"}
                                          content={"Будем отмечать Наурыз! Вас ждут национальные игры, музыка и угощения "}
                                          type={'Мероприятия'}
                                          data={"22.03.2021"}/>
                                <NavSlide img={img3}
                                          title={"ОНЛАЙН ПРОФОРИЕНТАЦИЯ"}
                                          content={"В быстроменяющемся современном мире выбор профессии одна из важнейших и сложных задач. Подготовка молодежи к выбору профессии - профориентация, полезный и важный вид занятий, помогающий определиться с выбором профессии."}
                                          type={'ZOOM-КОНФЕРЕНЦИЯ'}
                                          data={"10.04.2021"}/>
                                <NavSlide img={img4}
                                          title={"Прощай коворкинг, привет новый офис @qala.lab"}
                                          content={"Здесь проводили фотосушку сообщество плёночных фотографов «Свет», играли Настольщики @boardgamesaktobe, работал @turlanovaidar , наш первый резидент коворкинга, проводили фотосессии так и профессиональные фотографы, так и наши посетители арт-пространства. "}
                                          type={'НОВОСТИ'}
                                          data={"27.04.2021"}/>
                            </Slider>
                        </div>
                    </div>

                </div>
            </div>
        );
    }
}
